.main__chatlist {
    border-right: 1px solid #D0D0D0;
}

.chatlist__heading {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.chatlist-avatar__wrapper {
    margin: 20px 0 10px 20px;
    position: relative;
}
.userPicture {
    height: 60px;
    object-fit: cover;
    border-radius: 50%;

}
.checkmark {
    position: absolute;
    mix-blend-mode: multiply;
    top: 41px;
    left: 35px;
}
.search_wrap {
    position: relative;
    background-color: #fff;
    border-radius: 17px;
    margin: 15px 12px 15px 12px;
}

.search_wrap input {
    background-color: transparent;
    border: none;
    padding: 11px 0px;
    outline: none;
}

.search-btn {
    border: none;
    background-color: transparent;
    color: #909090;
    outline: none;
    width: 10%;
    cursor: pointer;
    font-size: 15px;
}

.close-btn {
    border: none;
    background-color: transparent;
    color: #909090;
    outline: none;
    cursor: pointer;
    font-size: 15px;
    margin-left: 47%;
    position: absolute;
    top: 10px;
    left: 180px;
}

.chatlist_contacts {
    background-color: #fff;
    border-top: 1px solid #D0D0D0;
}

.chatlist_title {
    color: #00BFFF;
    font-weight: 300;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin: 40px 0px 40px 24px;
}